import React, { useContext } from 'react';
import Title from '../form/Title';
import Card from '../Card';
import MessagesLister from '../MessagesLister';
import { JARVISContext } from '../JARVIS';

const JARVISpage = ({ }) => {
    const { personality } = useContext(JARVISContext);

    return (
        <Card scrollBottom>
            <Title>{personality}</Title>
            <MessagesLister />
        </Card>
    );
}

export default JARVISpage;