import React from 'react';
import styled from 'styled-components';
import { pascalCase } from '../utility/utility';
import Icon from './Icon';

const Wrapper = styled.div`
    font-size: 1.5rem;
    font-weight: 800;
    padding: 0.3em 0;
    text-align: center;
`;
const I = styled(Icon)`
    margin-right: 0.5em;
`;

const Title = ({ children, ...props }) => (
    <Wrapper {...props}>
        {props.icon ? <I icon={props.icon} /> : null}
        {typeof children === 'string' ? pascalCase(children) : children}
    </Wrapper>
);


export default Title;