import { useContext, useEffect, useState } from "react";
import { useGlobalState } from "./stateProvider/StateProvider";
import Button, { BorderLessButton } from "./form/Button";
import { JARVISContext } from "./JARVIS";
import styled from "styled-components";
import Icon from "./form/Icon";
import APIKeyInput from "./APIKeyInput";
import Title from "./form/Title";
import Heading from "./form/Heading";
import { Section } from "./pages/Settings";

const Btn = styled(BorderLessButton)`
    margin: 1em 0;
    padding: 1em;
`

const Status = styled.span`
    color: ${({ theme, status }) => status ? theme.success : theme.error};
`

const SpotifyAuth = () => {
    const {
        state: {
            API_KEYS,
        },
        setSpotifyRefreshToken,
        setSpotifyClientId,
        setSpotifyClientSecret,
        setError
    } = useGlobalState();

    const { spotify, } = useContext(JARVISContext);

    const redirectURI = window.location.origin + window.location.pathname;
    const scopes = ['user-modify-playback-state', 'user-read-private', 'app-remote-control', 'user-read-playback-state', 'user-modify-playback-state'];

    const [refreshToken, setRefreshToken] = useState(API_KEYS.SPOTIFY.REFRESH_TOKEN);
    const [attempted, setAttempted] = useState(false);
    const [authenticated, setAuthenticated] = useState(false);
    const [device, setDevice] = useState(spotify.getPlaybackDevice());

    useEffect(() => {
        setDevice(spotify.getPlaybackDevice())
    }, [spotify]);

    useEffect(() => {
        const params = new URLSearchParams(window.location.search);
        const code = params.get('code');
        if (code) {
            fetchAccessToken(code);
            window.history.replaceState(null, null, redirectURI);
        }
    }, []);

    const redirectToAuthPage = () => {
        const authUrl = `https://accounts.spotify.com/authorize?client_id=${API_KEYS.SPOTIFY.CLIENT_ID}&response_type=code&redirect_uri=${encodeURIComponent(redirectURI)}&scope=${encodeURIComponent(scopes.join(' '))}`;
        window.location.href = authUrl;
    };

    const fetchAccessToken = (code) => {
        console.log('Callback Authenticating')
        setError('')
        spotify.authenticate(code)
            .then(res => {
                setSpotifyRefreshToken(res.refresh_token);
                setRefreshToken(res.refresh_token)
                setAuthenticated(true);
            })
            .catch(ex => {
                console.error(ex)
                setAuthenticated(ex);
                setError(ex.error.message)
            })
            .finally(() => {
                setAttempted(true);
                setDevice(spotify.getPlaybackDevice())
            })
    };

    const refresh = () => {
        setError('')
        spotify.refreshAccessToken(API_KEYS.SPOTIFY.REFRESH_TOKEN)
            .then(res => setAuthenticated(true))
            .catch(ex => {
                console.warn(ex)
                setAuthenticated(ex);
                setError(ex.error.message)
            })
            .finally(() => {
                setAttempted(true);
                setDevice(spotify.getPlaybackDevice())
            })
    };

    const getPlaybackDevice = () => {
        setError('')
        spotify.requestAccess()
            .then(() => {
                setDevice(spotify.getPlaybackDevice())
            })
            .catch(ex => {
                console.warn(ex)
                setError(ex.error.message)
            })
    };

    return (
        <>
            <Heading>Spotify {attempted && `(${authenticated === true ? 'Connected' : authenticated.error_description || authenticated.error})`}</Heading>
            <Section>
                {
                    API_KEYS.SPOTIFY.CLIENT_ID && API_KEYS.SPOTIFY.CLIENT_SECRET
                    && (
                        refreshToken ? (
                            <>
                                <Heading>Playback Device: <Status status={device}>{device ? 'Online' : 'Offline'}</Status></Heading>
                                <Btn
                                    onClick={getPlaybackDevice}
                                >{'Get Playback Device'}</Btn>
                                <Btn
                                    onClick={refresh}
                                    icon={attempted ? authenticated ? 'check' : 'cross' : false}
                                >{attempted ? authenticated ? 'Refreshed' : 'Failed to Refresh' : 'Click to Refresh Token'}</Btn>
                            </>
                        ) : (
                            <Button onClick={redirectToAuthPage}>Get Spotify Token</Button>
                        )
                    )
                }

                <APIKeyInput
                    label='Client ID'
                    value={API_KEYS.SPOTIFY.CLIENT_ID}
                    update={setSpotifyClientId}
                    placeholder='Enter Client ID'
                />
                <APIKeyInput
                    label='Client Secret'
                    value={API_KEYS.SPOTIFY.CLIENT_SECRET}
                    update={setSpotifyClientSecret}
                    placeholder='Enter Client Secret ID'
                />
            </Section>
        </>
    );
};

export default SpotifyAuth;