import React, { useContext, useState } from 'react';
import styled from 'styled-components';
import Heading from './form/Heading';
import { useGlobalState } from './stateProvider/StateProvider';
import { JARVISContext } from './JARVIS';
import { generateId, pascalCase } from './utility/utility';
import { BorderLessButton } from './form/Button';


const Img = styled.img`
    width: 256px;
    height: 256px;
    margin: 1em;
    border-radius: 1rem;
`

const ImageWrappers = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
`

const ImageWrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1em;
    max-width: 256px;
    justify-content: flex-end;
`
const ImageContainer = styled.div`
    position: relative;
`

const HeadingCss = styled(Heading)`
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
`

const DeleteButton = styled(BorderLessButton)`
    position: absolute;
    top: 1.5em;
    right: 1.5em;
    color: black;
`

const ImageDisplay = ({ index, id, prompt, image, resolution, ...props }) => {
    const {
        removeImage
    } = useGlobalState();

    const { downloadImageFromBase64 } = useContext(JARVISContext);

    const [focused, setFocused] = useState(false);

    const handleMouseOver = (bool) => {
        if (window.innerWidth > 800) setFocused(bool)
    }
    const handleFocus = (bool) => {
        if (window.innerWidth < 800) setFocused(bool)
    }

    return (
        <ImageWrapper
            key={generateId()}
            tabIndex={index}
            onMouseEnter={() => handleMouseOver(true)}
            onMouseLeave={() => handleMouseOver(false)}
            onFocus={() => handleFocus(true)}
            onBlur={() => handleFocus(false)}
        >
            <HeadingCss title={prompt}>{prompt}</HeadingCss>
            <ImageContainer>
                {focused
                    && <DeleteButton
                        icon='cross'
                        onClick={() => {
                            removeImage(id)
                        }}
                    />
                }
                <Img src={'data:image/png;base64, ' + image} />
            </ImageContainer>
            <span>{resolution}</span>
            <BorderLessButton
                onClick={() => downloadImageFromBase64(image, pascalCase(prompt))}
            >Download</BorderLessButton>
        </ImageWrapper>
    );
}

export default ImageDisplay;