import KwestGiver from "../api/KwestGiver"

class Spotify {
    constructor() {
        this.token = null;
        this.refreshToken = null;

        this.clientId = null;
        this.clientSecret = null;

        this.deviceId = null;

        this.spotifyVer = 'v1'
        this.Kwest = new KwestGiver();
        this.Kwest.addApiUrl(`https://api.spotify.com/${this.spotifyVer}/`)
    }

    setUnauthorized() {
        this.Kwest.setUnauthorized((url, config) => {
            return new Promise(async (resolve, reject) => {
                console.log('Attempting Log In.')
                this.refreshAccessToken()
                    .then(() => {
                        this.Kwest.fetchKwest(url, config)
                            .then(res => resolve(res))
                            .catch(ex => reject(ex))
                    })
                    .catch(ex => console.warn(ex))
            });
        })
    }

    getPlaybackDevice() {
        return this.deviceId;
    }

    setAccessToken(access_token) {
        this.token = access_token;
        this.Kwest.addHeaderKeys({ 'Authorization': 'Bearer ' + this.token })
    }
    setRefreshToken(refreshToken) {
        if (!refreshToken) return;
        this.refreshToken = refreshToken;
    }



    setClients(clientId, clientSecret) {
        this.clientId = clientId;
        this.clientSecret = clientSecret;
    }

    async getProfile() {
        return await this.Kwest.get(`me`);
    }

    async searchForTrack(track, artist) {
        const query = `track:${encodeURIComponent(track)} artist:${encodeURIComponent(artist)}`;
        const res = await this.Kwest.get(`search?q=${query}&type=track`);
        return res.tracks.items[0];
    }

    async searchForAlbum(album, artist) {
        const query = `album:${encodeURIComponent(album)} artist:${encodeURIComponent(artist)}`;
        const res = await this.Kwest.get(`search?q=${query}&type=album`);
        return res.albums.items[0];
    }

    async searchForArtist(artist) {
        const query = `artist:${encodeURIComponent(artist)}`;
        const res = await this.Kwest.get(`search?q=${query}&type=artist`);
        return res.artists.items[0];
    }

    playTracks(uri) {
        this.Kwest
            .put(`me/player/play?device_id=${this.deviceId}`, {
                uris: Array.isArray(uri) ? [...uri] : [uri],
            })
    }

    playMedia(uri) {
        this.Kwest
            .put(`me/player/play?device_id=${this.deviceId}`, {
                context_uri: uri,
            })
    }

    getArtist(artist) {
        this.Kwest.get(`artists/` + artist)
    }

    async getArtistTopTracks(artistId, market = 'US') {
        const res = await this.Kwest.get(`artists/${artistId}/top-tracks?market=${market}`);
        return res.tracks;
    }

    addTrackToQueue(trackUri, position = 0) {
        return this.Kwest
            .post(`me/player/queue?uri=${encodeURIComponent(trackUri)}&position=${position}`)
    }

    replayTrack() {
        this.Kwest.put(`me/player/seek?position_ms=0`)
    }

    skipTrack() {
        this.Kwest.post(`me/player/next`)
    }

    async getCurrentTrack() {
        const res = await this.Kwest.get(`me/player/currently-playing`)
        return res.item;
    }

    async playAction(track, artist) {
        const res = await this.searchForTrack(track, artist);
        await this.addTrackToQueue(res.uri);
        await this.delay(200);
        this.skipTrack();
    }

    async artistAction(artist) {
        const res = await this.searchForArtist(artist);
        this.playMedia(res.uri)
    }

    async albumAction(album, artist) {
        const res = await this.searchForAlbum(album, artist);
        this.playMedia(res.uri)
    }

    async likedAction() {
        const res = await this.getProfile();
        this.playMedia(res.uri + ':collection')
    }

    async authenticate(code, refreshToken) {
        return new Promise(async (resolve, reject) => {
            const bodyParams = refreshToken
                ? { grant_type: 'refresh_token', refresh_token: refreshToken }
                : { grant_type: 'authorization_code', code, redirect_uri: window.location.origin + window.location.pathname };

            KwestGiver.fetchQuest('https://accounts.spotify.com/api/token', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    Authorization: `Basic ${btoa(`${this.clientId}:${this.clientSecret}`)}`,
                },
                body: new URLSearchParams(bodyParams),
            })
                .then(async res => {
                    this.setTokens(res);
                    await this.requestAccess();
                    resolve(res);
                })
                .catch(ex => reject(ex))
        })
    }

    refreshAccessToken(refreshToken = this.refreshToken) {
        if (!refreshToken) {
            console.warn('No Spotify Refresh Token')
            return;
        }
        return this.authenticate(null, refreshToken)
    }

    setTokens(tokens) {
        this.setAccessToken(tokens.access_token);
        this.setRefreshToken(tokens.refresh_token);
        this.Kwest.addHeaderKeys({ 'Authorization': 'Bearer ' + this.token })
        this.setUnauthorized();
    }

    async requestAccess() {
        const id = await this.Kwest.get('me/player');
        if (id?.device?.id) {
            this.deviceId = id.device.id;
        }else {
            this.deviceId = null;
        }
        return this.deviceId;
    }

    delay(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }
}

export default Spotify;