import React, { useState } from 'react';
import styled, { keyframes } from 'styled-components';

import firstLayer from '../assets/JARVIS_1st.png'
import secondLayer from '../assets/JARVIS_2nd.png'
import thirdLayer from '../assets/JARVIS_3rd.png'
import forthLayer from '../assets/JARVIS_4th.png'
import fifthLayer from '../assets/JARVIS_5th.png'
import { randomIntBetween } from './utility/utility';
import { useGlobalState } from './stateProvider/StateProvider';

const LayerAnimation = () => {
    const startingAngle = randomIntBetween(0, 360);
    return (
        keyframes`
            0% {
                transform: rotate(${startingAngle}deg);
            }
            ${randomIntBetween(10, 20)}% {
                transform: rotate(${randomIntBetween(0, 360)}deg);
            }
            ${randomIntBetween(40, 50)}% {
                transform: rotate(${randomIntBetween(0, 360)}deg);
            }
            ${randomIntBetween(70, 80)}% {
                transform: rotate(${randomIntBetween(0, 360)}deg);
            }
            100% {
                transform: rotate(${startingAngle}deg);
            }
        `)
};

const ProfileIcon = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: 100%;
`;

const Wrapper = styled.div`
    position: absolute;
    width: 5vw;
    min-width: 100px;
    min-height: 100px;
    z-index: 10;
    right: 50px;
    bottom: 50px;

    display: flex;
    justify-content: center;
    align-items: center;

    @media (max-width: 800px) {
        top: 20px;
        right: 20px;
        bottom: unset;
    }
`;
const InnerGlow = styled.div`
    border-radius: 50%;
    box-shadow: 0 0 20px 20px #92c6d566;
`;
const Img = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 0 0.2rem #00c6ff);
    object-fit: cover;

    background-image: url(${({ img }) => img});
    background-repeat: no-repeat;
    background-position: center center;
    background-origin: content-box;
    background-size: contain;

    transform: rotate(${randomIntBetween(0, 360)}deg);
`;
const Layer = styled(Img)`
    animation: ${LayerAnimation} 3s infinite;
`;

const JARVISicon = ({ ...props }) => {
    const {
        state: {
            INTERACTING
        },
    } = useGlobalState();

    if (!INTERACTING) return null;

    return (
        <Wrapper>
            <InnerGlow />
            <Layer img={firstLayer} alt='First Layer' />
            <Layer img={secondLayer} alt='Second Layer' />
            <Layer img={thirdLayer} alt='Third Layer' />
            <Layer img={forthLayer} alt='Forth Layer' />
            <Layer img={fifthLayer} alt='Fifth Layer' />
        </Wrapper>
    );
}

export default JARVISicon;

export const Idle = ({ ...props }) => {
    return (
        <ProfileIcon {...props}>
            <Layer img={firstLayer} alt='First Layer' />
            <Layer img={secondLayer} alt='Second Layer' />
            <Layer img={thirdLayer} alt='Third Layer' />
            <Layer img={forthLayer} alt='Forth Layer' />
            <Layer img={fifthLayer} alt='Fifth Layer' />
        </ProfileIcon>
    );
}

export const Profile = ({ angles, ...props }) => {

    const [customAngles] = useState(angles)

    return angles ? (
        <ProfileIcon>
            <Img img={firstLayer} alt='First Layer' style={{ transform: `rotate(${customAngles.one}deg` }} />
            <Img img={secondLayer} alt='Second Layer' style={{ transform: `rotate(${customAngles.two}deg` }} />
            <Img img={thirdLayer} alt='Third Layer' style={{ transform: `rotate(${customAngles.three}deg` }} />
            <Img img={forthLayer} alt='Forth Layer' style={{ transform: `rotate(${customAngles.four}deg` }} />
            <Img img={fifthLayer} alt='Fifth Layer' style={{ transform: `rotate(${customAngles.five}deg` }} />
        </ProfileIcon>
    ) : (
        <ProfileIcon>
            <InnerGlow />
            <Layer img={firstLayer} alt='First Layer' />
            <Layer img={secondLayer} alt='Second Layer' />
            <Layer img={thirdLayer} alt='Third Layer' />
            <Layer img={forthLayer} alt='Forth Layer' />
            <Layer img={fifthLayer} alt='Fifth Layer' />
        </ProfileIcon>
    );
}