class KwestGiver {
    constructor(url) {
        this.apiURL = url;
        this.headerKeys = [];
        this.unauthorized = null;

        this.patchOp = {
            update: 'replace',
            add: 'add',
            remove: 'remove',
            copy: 'copy',
            move: 'move',
            test: 'test'
        }
        this.methods = {
            get: 'GET',
            post: 'POST',
            put: 'PUT',
            patch: 'PATCH',
            delete: 'DELETE'
        }
    }

    addApiUrl(url) {
        this.apiURL = url;
    }

    // FETCH
    fetchKwest(url, config) {
        console.log(`%cHamQuest: %c${url}`, 'color:blue', 'color:red');
        if (!url.match('https://|http://')) url = this.apiURL ? this.apiURL + url : url;

        if (!config.headers || !config.headers['Content-Type']) {
            Object.assign(
                config.headers || config,
                config.headers ? { 'Content-Type': 'application/json' } : { headers: { 'Content-Type': 'application/json' } }
            )
        }

        this.headerKeys.forEach(e => Object.assign(config.headers, e));

        const errorType = {
            server: 'Server Error',
            loggedOut: 'Logged Out',
            message: 'Error'
        }

        return new Promise((turnIn, abandonKwest) => {
            fetch(url, config)
                .then(async res => {
                    if (!res.ok) {
                        try {
                            if (res.status === 401) {
                                if (this.unauthorized) {
                                    turnIn(this.unauthorized(url, config))
                                } else {
                                    const error = await res.json()
                                    abandonKwest(error)
                                }
                            } else {
                                const error = await res.json()
                                abandonKwest(error)
                            }
                        }
                        catch (ex) {
                            abandonKwest(await res.text());
                        }
                    }
                    if (res.status === 204) {
                        turnIn(true)
                    }
                    const contentType = res.headers.get('Content-Type')
                    if (contentType?.match('application/json')) {
                        return res.json();
                    } else {
                        turnIn(res)
                    }
                })
                .then(res => {
                    if (res.success === true) {
                        turnIn(res.resultData);
                    } else if (res.success === false) {
                        const errorMessage = {
                            error: errorType.message,
                            blob: res.errorMsg || 'An Error has occurred...'
                        };
                        abandonKwest(errorMessage)
                    } else {
                        turnIn(res);
                    }
                })
                .catch(ex => {
                    abandonKwest(ex);
                })
        })
            .catch((e) => {
                throw e
            })
    }

    get(url, config = {}) {
        return this.kwestBoard(url, config, this.methods.get);
    }

    getQuery(url, query, config = {}) {
        url += '?' + Object
            .entries(query)
            .map(([key, value]) => `${key}=${encodeURIComponent(value)}`)
            .join('&');

        return this.kwestBoard(url, config, this.methods.get);
    }

    post(url, body, config = {}) {
        config.body = body;
        return this.kwestBoard(url, config, this.methods.post);
    }

    put(url, body, config = {}) {
        config.body = body;
        return this.kwestBoard(url, config, this.methods.put);
    }

    patch(url, body, operation, config = {}) {
        config.body = body;

        if (body) {
            let patchBody = [];
            Object
                .entries(body)
                .forEach(([key, value]) => {
                    let objectProp = {
                        op: operation
                    }
                    if (operation === this.patchOp.add || operation === this.patchOp.update || operation === this.patchOp.test) {
                        objectProp.path = `/${key}`;
                        objectProp.value = value;
                    } else if (operation === this.patchOp.remove) {
                        objectProp.path = `/${key}`;
                    } else if (operation === this.patchOp.copy || operation === this.patchOp.move) {
                        objectProp.from = `/${key}`;
                        objectProp.path = `/${value}`;
                    }
                    patchBody.push(objectProp);
                })
            config.body = patchBody;
        }
        return this.kwestBoard(url, config, this.methods.patch);
    }

    delete(url, config = {}) {
        return this.kwestBoard(url, config, this.methods.delete);
    }

    kwestBoard(url, config, method) {
        if (config.body instanceof Object) {
            config.body = JSON.stringify(config.body)
        }
        return new Promise((resolve, reject) => {
            this.fetchKwest(url, this.updateMethod(config, method))
                .then(res => resolve(res))
                .catch(ex => reject(ex))
        })
    }

    updateMethod(c, m) {
        return Object.assign(c, { method: m });
    }

    setUnauthorized(func) {
        this.unauthorized = func;
    }

    clearUnauthorized() {
        this.unauthorized = null;
    }

    // WEBSOCKET
    webSocket(url) {
        const socket = new WebSocket(url);

        socket.onopen = function (e) {
            // console.log(`%c[open]`, 'color:green', `Connection established`);
        };

        socket.onmessage = function (event) {
            // console.log(`%c[message]`, 'color:blue', `Data received from server: ${event.data}`);
            try {
                const msg = JSON.parse(event.data);
                if (msg.socketId) {
                    socket.id = msg.socketId;
                    socket.ready();
                } else {
                    socket.messaged(msg)
                }
            }
            catch {
                socket.messaged(event.data)
            }
        };

        socket.onclose = function (event) {
            if (event.wasClean) {
                // console.log(`%c[close]`, 'color:red', `Connection closed cleanly, code=${event.code} reason=${event.reason}`);
                socket.closed();
            } else {
                // console.log(`%c[close]`, 'color:red', `Connection died`);
            }
        };

        socket.onerror = function (error) {
            // console.log(`%c[ERROR]`, 'color:red', error);
            socket.error(error);
        };

        return socket;
    }

    // HEADER KEYS
    showHeaderKeys() {
        console.log([...this.headerKeys]);
    }

    hasHeaderKeys(k) {
        return this.headerKeys.find(e => e.hasOwnProperty(k));
    }

    clearHeaderKeys() {
        this.headerKeys.length = 0;
    }

    addHeaderKeys(k) {
        this.headerKeys.push(k);
    }

    replaceHeaderKey(k) {
        const key = Object.keys(k)[0];
        let authKey = this.headerKeys.find(e => e[key]);
        if (authKey) {
            authKey[key] = k[key];
        } else {
            this.addHeaderKeys(k);
        }
    }

    removeHeaderKey(k) {
        const key = Object.keys(k)[0];
        let authKey = this.headerKeys.findIndex(e => e[key]);
        if (authKey !== -1) {
            this.headerKeys.splice(authKey, 1);
        }
    }


    static fetchQuest(url, config) {
        const kwest = new KwestGiver()
        return kwest.fetchKwest(url, config);
    }
    static get(url, config = {}) {
        const kwest = new KwestGiver();
        return kwest.get(url, config);
    }
    static getQuery(url, query, config = {}) {
        const kwest = new KwestGiver();
        return kwest.getQuery(url, query, config);
    }
    static post(url, body, config = {}) {
        const kwest = new KwestGiver();
        return kwest.post(url, body, config);
    }
    static put(url, body, config = {}) {
        const kwest = new KwestGiver();
        return kwest.put(url, body, config);
    }
    static patch(url, body, operation, config = {}) {
        const kwest = new KwestGiver();
        return kwest.patch(url, body, operation, config)
    }
    static delete(url, config = {}) {
        const kwest = new KwestGiver();
        return kwest.delete(url, config);
    }
}

export default KwestGiver;