import styled from 'styled-components';
import { useGlobalState } from '../stateProvider/StateProvider';
import HotKeyListener from '../api/HotKeyListener';
import AskJARVIS from '../AskJARVIS';
import CheckBox from '../form/CheckBox';
import Row from './Row';
import Crawler from '../Crawler';

const ContentWrapper = styled.div`
    padding: 2em;
    overflow: hidden;
    width: initial;
`
const Wrapper = styled.div`
    width: -webkit-fill-available;
    transition: all 0.2s;
    display: flex;
    align-items: center;

    padding: 0.2em 1em;
    background: ${({ theme }) => theme.lighter};
`

const Margin = styled.div`
    width: 100%;
    margin: 0em 0.5em;
    overflow: hidden;
    transition: all 0.2s;
    max-height: 1.5em;
    height: ${({ displaying }) => displaying ? 1.5 : 0.2}em;
`

const Error = styled.div`
    color: ${({ theme }) => theme.error};
`


const StatusBar = ({ children, ...props }) => {
    const {
        state: {
            CONFIG,
            ERROR
        },
    } = useGlobalState();

    return (
        <Wrapper>
            <Margin
                displaying={ERROR ? 1 : 0}
            >
                {
                    ERROR &&
                    <Crawler>
                        <Error>
                            ERROR: {ERROR}
                        </Error>
                    </Crawler>
                }
            </Margin>
        </Wrapper>
    );
}

export default StatusBar;
