import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
    background: none;
    color: inherit;
    border: none;
    font: inherit;
    outline: inherit;
    display: flex;
    align-items: center;
    border-radius: 0.2em;
    margin: 0.2rem 0;
    padding: 0.7rem 1rem;
    transition: box-shadow 0.5s;
    background: ${({ theme }) => theme.lighter};

    ${({ focus }) => focus ? `
        box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.40);
    ` : ''}
`
const TextAreaCSS = styled.textarea`
    background: none;
    color: inherit;
    border: none;
    font: inherit;
    outline: inherit;
    display: flex;
    font-size: 1em;
    line-height: 1.5em;
    align-items: center;
    border-radius: 0.2em;

    transition: box-shadow 0.5s;
    resize: none;
    background: ${({ theme }) => theme.lighter};

    overflow-y: hidden;
    max-height: 300px;

    width: 100%;
    word-break: break-word;

    
    &:disabled {
        opacity: 0.6;
    }
`;

const TextArea = ({ onUpdate, ...props }) => {
    const textAreaRef = useRef(null);

    const [focus, setFocus] = useState(false);

    useLayoutEffect(() => {
        updateHeight(props.value)
    }, [props.value])

    const handleChange = (e) => {
        updateHeight(e?.target?.value)
    };

    const updateHeight = (value) => {
        const fontSize = parseInt(window.getComputedStyle(textAreaRef.current).fontSize);
        const maxHeight = parseInt(window.getComputedStyle(textAreaRef.current).maxHeight);

        let lines = (value.match(/\n/g) || []).length + 1 || 1
        textAreaRef.current.style.height = `${lines * 1.5}em`;

        if (textAreaRef.current.scrollHeight > (maxHeight - (fontSize))) {
            textAreaRef.current.style.height = `${maxHeight}px`;
            textAreaRef.current.style.overflowY = `overlay`;
            return;
        };

        let error = 20;
        while (textAreaRef.current.scrollHeight > textAreaRef.current.clientHeight) {
            lines++;
            textAreaRef.current.style.height = `${lines * 1.5}em`;

            error--;
            if (error === 0) {
                console.warn('while error')
                break;
            }
        }
    }

    const className = props.className;
    delete props.className;

    return (
        <Wrapper
            className={className}
            focus={focus}
        >
            <TextAreaCSS
                wrap='hard'
                ref={textAreaRef}
                onChange={handleChange}
                onFocus={() => setFocus(true)}
                onBlur={() => setFocus(false)}
                {...props}
            />
        </Wrapper>

    )
};

export default TextArea;
