import React from 'react';
import styled from 'styled-components';
import { SpanLabel } from '../APIKeyInput';

const InputCss = styled.input`
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    outline: inherit;
    display: flex;
    height: 2rem;
    align-items: center;
    padding: 0.3rem 1rem;
    border-radius: 0.2em;
    transition: all 0.2s;
    margin: 0.2rem 0;
    overflow: hidden;
    white-space: nowrap;

    &:hover {
        cursor: pointer;
    }
`;

const Label = styled.label`
    display: flex;
    align-items: center;
    border-radius: 0.2rem;

    &:hover {
        cursor: pointer;
        text-decoration: underline;
    }
`;

const CheckBox = ({ ...props }) => {
    return (
        <Label>
            {props.label && <SpanLabel>{props.label}</SpanLabel>}
            <InputCss type='checkbox' {...props} />
        </Label>
    )
};

export default CheckBox;