import React, { useContext, useEffect, useState } from 'react';
import { randomIntBetween } from './utility/utility';
import { Profile } from './JARVISicon';
import { JARVISContext } from './JARVIS';
import Message from './Message';

const MessagesLister = ({ ...props }) => {

    const { roles, messages } = useContext(JARVISContext);

    const [profile] = useState(
        <Profile angles={{
            one: randomIntBetween(0, 360),
            two: randomIntBetween(0, 360),
            three: randomIntBetween(0, 360),
            four: randomIntBetween(0, 360),
            five: randomIntBetween(0, 360)
        }} />
    );

    return (
        messages
            .filter(e => e.role !== roles.system)
            .map((e, i) => (
                <Message
                    key={e.id}
                    role={e.role}
                    profileImg={profile}
                    content={e.content}
                    id={e.id}
                    last={i === messages.length - 2}
                    messageIndex={i}
                    {...e}
                />
            ))
    )
}

export default MessagesLister;