import React from 'react';
import Title from '../form/Title';
import Card from '../Card';
import P5Frame from '../p5js/P5Frame';
import Row from '../layout/Row';
import Col from '../layout/Col';
import Input from '../form/Input';
import Hls from 'hls.js';
import { useState } from 'react';
import { useRef } from 'react';
import { useEffect } from 'react';
import KwestGiver from '../api/KwestGiver';
import { useGlobalState } from '../stateProvider/StateProvider';
import Heading from '../form/Heading';
import Button from '../form/Button';
import styled from 'styled-components';

const VideoWrapper = styled.div`
    display: flex;
    align-items: center;
    flex-grow: 1;
    justify-content: center;
`
const Video = styled.video`
    width: 90%;
`


const ProKzyPage = ({ }) => {
    const {
        state: { },
        setError
    } = useGlobalState();

    const [playing, setPlaying] = useState(false);
    const [postBody, setPostBody] = useState({});
    const [buffering, setBuffering] = useState(false);
    const videoPlayerRef = useRef(null);
    let timeoutId;
    let controller;
    let hls;

    const host = 'https://prokzy.azurewebsites.net';
    const m3u8 = 'https://prokzy.azurewebsites.net/segments/index.m3u8';

    useEffect(() => {
        const initializeVideoPlayer = () => {
            if (Hls.isSupported()) {
                if (!hls) {
                    hls = new Hls();
                    hls.on(Hls.Events.ERROR, (event, data) => {
                        console.error("HLS error:", event, data);
                    });
                    hls.on(Hls.Events.FRAG_LOADED, (event, data) => {
                        console.log("HLS fragment loaded:", event, data);
                    });
                }

                hls.loadSource(m3u8);
                hls.attachMedia(videoPlayerRef.current);

                hls.on(Hls.Events.MANIFEST_PARSED, () => {
                    console.log("HLS manifest parsed");
                    videoPlayerRef.current.play();
                    setPlaying(true);
                });
            } else if (videoPlayerRef.current.canPlayType("application/vnd.apple.mpegurl")) {
                videoPlayerRef.current.src = m3u8;
                videoPlayerRef.current.addEventListener("loadedmetadata", () => {
                    console.log("Loaded metadata");
                    videoPlayerRef.current.play();
                });
                videoPlayerRef.current.addEventListener("error", (event) => {
                    console.error("Video player error:", event);
                });
            } else {
                console.error(
                    "HLS is not supported and native HLS playback is not supported.",
                );
            }
        };

        if (postBody.url) {
            console.log("Searching:", postBody);
            const streamURL = host + `/stream`;

            if (hls) {
                hls.destroy();
                hls = null;
                videoPlayerRef.current.removeAttribute("src");
                videoPlayerRef.current.load();
            }

            if (controller) {
                controller.abort();
            }

            controller = new AbortController();

            fetch(streamURL, {
                method: 'POST',
                body: JSON.stringify(postBody),
                headers: {
                    'Content-Type': 'application/json',
                },
                signal: controller.signal,
            })
                .then((res) => {
                    reset();
                })
                .catch((ex) => {
                    if (!ex.message.includes("signal is aborted without reason")) {
                        setBuffering(false);
                        clearTimeout(timeoutId);
                        setError(ex);
                    }
                });

            timeoutId = setTimeout(() => {
                initializeVideoPlayer();
                setBuffering(false);
            }, 10000);
        }

        return () => {
            if (controller) {
                controller.abort();
            }
            if (hls) {
                hls.destroy();
                hls = null;
                videoPlayerRef.current.removeAttribute("src");
                videoPlayerRef.current.load();
            }
        };
    }, [postBody]);

    useEffect(() => {
        if (playing) return;
        if (controller) {
            controller.abort();
        }
        if (hls) {
            hls.destroy();
            videoPlayerRef.current.removeAttribute("src");
            videoPlayerRef.current.load();
        }
    }, [playing])

    const reset = () => {
        setError('');
        setBuffering(false);
        postBody({});
    };

    const submitForm = (e) => {
        const formData = new FormData(e.target);
        const jsonObject = {};
        formData.forEach((value, key) => {
            jsonObject[key] = value;
        });
        e.preventDefault();

        setPostBody({ ...jsonObject });
    }


    return (
        <Card>
            <Title>ProKzy</Title>
            <Row style={{ width: '100%', alignItems: 'flex-start' }}>
                <form onSubmit={submitForm}>
                    <Col>
                        <Input
                            name='url'
                            placeholder='Stream URL'
                        />
                        <Heading>Quality</Heading>
                        <label>
                            <Row>
                                <span>Best</span>
                                <Input
                                    type='radio'
                                    name='quality'
                                    value='best'
                                />
                            </Row>
                        </label>
                        <label>
                            <Row>
                                <span>Worst</span>
                                <Input
                                    type='radio'
                                    name='quality'
                                    value='worst'
                                />
                            </Row>
                        </label>
                        <Button type='submit' onClick={() => setBuffering(true)}>{buffering ? 'Buffering Stream' : 'Load Stream'}</Button>
                        {playing && <Button onClick={() => setPlaying(false)}>Stop Stream</Button>}
                    </Col>
                </form>

                <VideoWrapper>
                    <Video
                        id="main_stream"
                        controls
                        ref={videoPlayerRef}
                        className="main_video"
                    >
                        <track kind="captions" srcLang="en" label="English captions" />
                    </Video>
                </VideoWrapper>
            </Row>
        </Card>
    );
}

export default ProKzyPage;