import React, { useContext, useState } from 'react';
import { useGlobalState } from '../stateProvider/StateProvider';
import Title from '../form/Title';
import Card from '../Card';
import APIKeyInput, { SpanLabel } from '../APIKeyInput';
import Heading from '../form/Heading';
import { JARVISContext } from '../JARVIS';
import { generateId, pascalCase } from '../utility/utility';
import Select from '../form/Select';
import styled from 'styled-components';
import CheckBox from '../form/CheckBox';
import SpotifyAuth from '../SpotifyAuth';
import Button, { BorderLessButton } from '../form/Button';
import { clearAllPersistedState, clearCache } from '../stateProvider/UsePersistedState';
import Row from '../layout/Row';
import Input from '../form/Input';
import TextArea from '../form/TextArea';


export const Section = styled.div`
    padding: 0.5em;
`

const Settings = ({ }) => {
    const {
        state: {
            API_KEYS,
            CONFIG,
            VERSION
        },
        setGPTKey,
        toggleHotkey,
    } = useGlobalState();

    const {
        model,
        setModel,
        getModels,
        personality,
        getPersonalities,
        setPersonality,
        plugins,
        toggleExternal,
        spotify,
        getSystemMessage,
        setSystemMessage,
        defaultSystem,
        imageSize,
        setImageSize,
        getImageSizes,
        stream,
        toggleStream,
        initJARVIS
    } = useContext(JARVISContext);

    return (
        <Card>
            <Title icon='cog'>Settings</Title>

            <Heading>GPT</Heading>
            <Section>
                <APIKeyInput
                    label='Secret Key'
                    value={API_KEYS.GPT.KEY}
                    update={setGPTKey}
                    placeholder='Enter GPT Secret Key'
                />

                <Select
                    label='Model'
                    value={model}
                    onChange={(e) => {
                        setModel(e.target.value);
                    }}
                >
                    {getModels().map((e) => <option key={generateId()} value={e}>{e}</option>)}
                </Select>

                <Select
                    label='Personality'
                    value={personality}
                    onChange={(e) => setPersonality(e.target.value)}
                >
                    {getPersonalities().map((e) => <option key={generateId()} value={e}>{e}</option>)}
                </Select>

                <CheckBox
                    label='Stream Chat'
                    checked={stream}
                    onChange={() => toggleStream()}
                />
            </Section>


            <SpotifyAuth />

            <Heading>External Plugins</Heading>
            <Section>
                {
                    API_KEYS.GPT.KEY && <>
                        <CheckBox
                            label='DALL·E'
                            checked={plugins.DALLE}
                            onChange={() => {
                                toggleExternal('DALLE');
                            }}
                        />

                        <Section>
                            <Select
                                label='Image Size'
                                value={imageSize}
                                onChange={(e) => setImageSize(e.target.value)}
                            >
                                {getImageSizes().map((e) => <option key={generateId()} value={e}>{e}</option>)}
                            </Select>
                        </Section>
                    </>
                }
                <CheckBox
                    label='Spotify'
                    checked={plugins.spotify}
                    onChange={() => {
                        if (!plugins.spotify) {
                            spotify.refreshAccessToken(API_KEYS.SPOTIFY.REFRESH_TOKEN)
                        }
                        toggleExternal('spotify');
                    }}
                />
                <CheckBox
                    label='Browser'
                    checked={plugins.browser}
                    onChange={() => toggleExternal('browser')}
                />
                {
                    window.innerWidth > 800 &&
                    <CheckBox
                        label='Hotkey listener'
                        checked={CONFIG.HOTKEY}
                        onChange={() => toggleHotkey()}
                    />
                }
            </Section>

            <Heading>Custom Personality</Heading>
            <Section>
                <TextArea
                    value={getSystemMessage() === defaultSystem ? '' : getSystemMessage()}
                    onInput={(e) => {
                        setSystemMessage(
                            e.target.value.length
                                ? e.target.value
                                : defaultSystem
                        )
                    }}
                    placeholder='Set a custom Personality.'
                    style={window.innerWidth > 800 ? { width: '20vw' } : {}}
                />

                {(getSystemMessage() !== defaultSystem) &&
                    <BorderLessButton
                        onClick={() => {
                            setSystemMessage(defaultSystem)
                        }}>Clear Custom Personality</BorderLessButton>
                }
            </Section>

            <Heading>Controls</Heading>
            <Section>
                <Row>
                    <Button
                        onClick={() => {
                            clearCache();
                            window.location.reload();
                        }}
                    >Clear Cache</Button>
                    <Button
                        onClick={() => {
                            clearAllPersistedState();
                            window.location.reload();
                        }}
                    >Clear Storage</Button>
                    <Button
                        onClick={() => initJARVIS()}
                    >Rebuild AI</Button>
                </Row>
                <Row>
                    <SpanLabel>App Ver.</SpanLabel>
                    <Input disabled value={VERSION} />
                </Row>
            </Section>
        </Card >
    );
}

export default Settings;