import React, { useState, useEffect, useCallback } from 'react';
import KwestGiver from './KwestGiver';
import { useGlobalState } from '../stateProvider/StateProvider';
import styled from 'styled-components';
import CountDown from '../CountDown';

const Wrapper = styled.div`
    white-space: nowrap;
    color: ${({ status, theme }) => !status ? theme.success : theme.error}
`

function HotKeyListener() {
    const {
        state: {
            CONFIG
        },
        toggleListening
    } = useGlobalState();

    const [retry, setRetry] = useState(false);
    const [retries, setRetries] = useState(1);
    const [retryTimer, setRetryTimer] = useState(5);
    const [status, setStatus] = useState('');

    const [socket, setSocket] = useState(null);

    const connectToWS = () => {
        const Kwest = new KwestGiver();
        const listener = Kwest.webSocket('ws://localhost:8888');

        setStatus('Connecting...');
        setRetry(false)

        listener.error = e => {
            console.log(e);
            if (retries < 4) {
                setStatus('Failed. Retrying in: ');
                setRetry(true);
                setRetries(retries => retries + 1)
            } else {
                setStatus('Offline');
            }
        };

        listener.ready = () => {
            setStatus('ONLINE');
            setRetry(false);
        };

        listener.messaged = e => {
            if (e.HOTKEY === 'F6') {
                toggleListening();
            }
        };

        listener.closed = e => {
            setStatus('Disconnected');
        }

        setSocket(listener)
    };

    useEffect(() => {
        if (CONFIG.HOTKEY) {
            connectToWS();
        } else if (socket) {
            socket.close();
        }
    }, [CONFIG.HOTKEY]);

    return (
        <Wrapper status={retry}>
            HOTKEY: {status}
            {
                retry ?
                    <CountDown
                        time={retryTimer}
                        callback={() => {
                            console.log('CallingBack')
                            setRetryTimer((retryTimer) => retryTimer * 2)
                            connectToWS()
                        }}
                    /> : null
            }
        </Wrapper>
    )

}

export default HotKeyListener;