import React from 'react';
import styled from 'styled-components';

export const InputCss = styled.input`
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    outline: inherit;
    display: flex;
    height: 2rem;
    align-items: center;
    padding: 0.3rem 1rem;
    border-radius: 0.2em;
    transition: all 0.2s;
    margin: 0.2rem 0;
    overflow: hidden;
    white-space: nowrap;

    background: ${({ theme }) => theme.lighter};

    &:disabled {
        opacity: 0.6;
    }
`;


const Input = ({ ...props }) => {
    return (
        <InputCss {...props} />
    )
};

export default Input;