import { useContext, useEffect, useState } from 'react';
import styled from 'styled-components';
import { BorderLessButton } from './form/Button';
import { useGlobalState } from './stateProvider/StateProvider';
import { JARVISContext } from './JARVIS';
import TextArea from './form/TextArea';
import { useNavigate } from 'react-router-dom';

const Wrapper = styled.div`
    display: inline-flex;
    position: relative;
    width: calc(100% - 170px);
    transition: all 0.2s;

    @media (max-width: 800px) {
        width: calc(100% - ${({ clear }) => clear ? 40 : 0}px);
    }
`
const InputCSS = styled(TextArea)`
    width: 100%;
    padding-right: 50px;
    font-size: 1.1em;
`
const Button1 = styled(BorderLessButton)`
    position: absolute;
    right: 10px;
    top: calc(100% - 2em);
    height: 30px;
    transform: translateY(-50%);
`
const Button2 = styled(BorderLessButton)`
    position: absolute;
    right: -40px;
    top: calc(100% - 2em);
    height: 30px;
    transform: translateY(-50%);

    @media (max-width: 800px) {
        right: -40px;
    }
`

const AskJARVIS = ({ ...props }) => {
    const {
        state: {
            CONFIG
        },
        addVoiceChat,
        setError,
        toggleGPTEnabled
    } = useGlobalState();

    const navigate = useNavigate();

    const { messages, personality, askCHAT, clearMessages } = useContext(JARVISContext);

    const [inputValue, setInputValue] = useState('');

    const handleUpdate = () => {
        if (!inputValue) askCHAT();
        addVoiceChat(inputValue);
        setInputValue('');
        navigate('/JARVIS');
    };

    const handleKeyPress = (e) => {
        if (e.shiftKey && e.key === "Enter") {
        } else if (e.key === 'Enter') {
            e.preventDefault();
            handleUpdate();
            if (window.innerWidth < 800)
                document.activeElement.blur();
        }
    };

    return (
        <Wrapper
            clear={messages.length > 1}
        >
            <InputCSS
                type='text'
                placeholder={CONFIG.GPT.ENABLED ? `Send a message to ${personality}` : 'GPT Disabled.'}
                disabled={!CONFIG.GPT.ENABLED}
                value={inputValue}
                onInput={(event) => setInputValue(event.target.value)}
                onKeyDown={handleKeyPress}
            />
            {
                <Button1
                    onClick={CONFIG.GPT.ENABLED ? handleUpdate : toggleGPTEnabled}
                    icon={CONFIG.GPT.ENABLED ? 'paperPlane' : 'power'}
                    title='Submit'
                />
            }
            {
                messages.length > 1 &&
                <Button2
                    onClick={() => {
                        clearMessages();
                        setError(null);
                    }}
                    icon='trash'
                    title='Clear Chat'
                />
            }
        </Wrapper>
    );
}

export default AskJARVIS;
