import React, { useContext, useState } from 'react';
import Title from '../form/Title';
import Card from '../Card';
import { useGlobalState } from '../stateProvider/StateProvider';
import styled from 'styled-components';
import Heading from '../form/Heading';
import { JARVISContext } from '../JARVIS';
import { generateId } from '../utility/utility';
import ImageDisplay from '../ImageDisplay';
import Row from '../layout/Row';
import Col from '../layout/Col';
import { BorderLessButton } from '../form/Button';
import TextArea from '../form/TextArea';

const ImageWrappers = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
`
const BLBtn = styled(BorderLessButton)`
    margin: 0 0.2em;
`
const Span = styled.span`
    margin-right: 0.2em;
`

const GeneratedImages = ({ }) => {
    const {
        state: {
            API_KEYS,
            GENERATED_IMAGES,
            INTERACTING
        },
        addImage,
        setInteraction,
        setError
    } = useGlobalState();

    const { personality, imageSize, generateImage } = useContext(JARVISContext);

    const [prompt, setPrompt] = useState('');

    const handleSubmit = () => {
        setInteraction(true);
        setError('');
        generateImage(prompt)
            .then(res => {
                addImage({
                    prompt: prompt,
                    image: res,
                    resolution: imageSize,
                });
            })
            .catch(ex => {
                setError(ex?.error?.message || ex?.error?.code || 'Something went wrong...')
            })
            .finally(() => {
                setInteraction(false);
            })
    }

    const handleKeyPress = (e) => {
        if (e.shiftKey && e.key === "Enter") {
        } else if (e.key === 'Enter') {
            e.preventDefault();
            handleSubmit();
        }
    };

    return (
        <Card>
            <Title>Images</Title>
            {
                API_KEYS.GPT.KEY &&
                <Col
                    style={{ width: '100%' }}
                >
                    <Row style={{ minHeight: '30px' }}>
                        <Span>Generate an Image:</Span>
                        {
                            prompt &&
                            <BLBtn
                                onClick={() => setPrompt('')}
                                icon='trash'
                                title='Clear Prompt'
                            />
                        }
                        {
                            (prompt && !INTERACTING) &&
                            <BLBtn
                                title='Submit'
                                onClick={() => handleSubmit()}
                                icon='paperPlane'
                            />
                        }
                    </Row>
                    <TextArea
                        value={prompt}
                        onChange={e => setPrompt(e.target.value)}
                        disabled={INTERACTING}
                        placeholder='Image Prompt'
                        onKeyDown={handleKeyPress}
                    />
                </Col>
            }

            <ImageWrappers>
                {GENERATED_IMAGES.length < 1
                    ? <Heading>Have {personality} generate you some images!</Heading>
                    : GENERATED_IMAGES
                        .filter(e => e)
                        .map((e, i) => (
                            <ImageDisplay
                                key={generateId()}
                                index={i + 1}
                                id={e.id}
                                prompt={e.prompt}
                                image={e.image}
                                resolution={e.resolution}
                            />
                        ))
                }
            </ImageWrappers>

        </Card>
    );
}

export default GeneratedImages;