import React from 'react';
import Title from '../form/Title';
import Card from '../Card';
import P5Frame from '../p5js/P5Frame';


const CarsPage = ({ }) => {

    return (
        <Card>
            <Title>Cars.js</Title>
            <P5Frame title='Cars.js' src='./p5/Cars/index.html' additionalKeys={['G']} />
        </Card>
    );
}

export default CarsPage;