import React from 'react';
import {
    Routes,
    Route,
} from 'react-router-dom';
import Layout from '../layout/Layout';
import Home from '../pages/Home';
import JARVISpage from '../pages/JARVISpage';
import Settings from '../pages/Settings';
import GeneratedImages from '../pages/GeneratedImages';
import AntsPage from '../pages/AntsPage';
import CarsPage from '../pages/CarsPage';
import ProKzyPage from '../pages/ProKzy';

const ScrollToTop = ({ children, location }) => {
    React.useEffect(() => window.scrollTo(0, 0), [location]);
    return children;
};

const Router = () => {
    return (
        <ScrollToTop location={window.location.pathname}>
            <Layout>
                <Routes>
                    <Route index path={`/`} element={<Home />} />
                    <Route index path={`/JARVIS`} element={<JARVISpage />} />
                    <Route index path={`/Images`} element={<GeneratedImages />} />
                    <Route index path={`/Settings`} element={<Settings />} />
                    <Route index path={`/Ants`} element={<AntsPage />} />
                    <Route index path={`/Cars`} element={<CarsPage />} />
                    <Route index path={`/ProKzy`} element={<ProKzyPage />} />
                </Routes>
            </Layout>
        </ScrollToTop>
    );
}

export default Router;
