import React, { useEffect, useLayoutEffect, useState } from 'react';
import { useGlobalState } from './stateProvider/StateProvider';
import { useCallback } from 'react';
import JARVISModel from './GPT/JARVISModel';
import KwestGiver from './api/KwestGiver';
import styled from 'styled-components';
import Title from './form/Title';
import { generateId } from './utility/utility';
import Spotify from './GPT/Spotify';

const Page = styled.div`
    min-height: ${window.innerHeight}px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
`

export const JARVISContext = React.createContext();

const JARVIS = ({ children, ...props }) => {
    const {
        state: {
            CHAT,
            API_KEYS,
            CONFIG,
            INTERACTING,
        },
        clearChat,
        setInteraction,
        toggleGPTEnabled,
        addImage,
        setError
    } = useGlobalState();

    // GPT
    const [Kwest] = useState(new KwestGiver());

    const [API_URL, setAPI_URL] = useState('https://api.openai.com/v1/chat/completions');
    const [API_KEY, setAPI_KEY] = useState(null);

    const setUrl = (url) => setAPI_URL(url);


    const setAPIKey = (key) => setAPI_KEY(key);


    const setModel = (model) => setModal(model);


    const ask = (body = {}) => {
        return new Promise((resolve, reject) => {
            body = {
                content: { ...body },
                GPT_key: API_KEY,
                personality: {
                    personality: personality,
                    plugins: plugins,
                }
            }

            Kwest.post(API_URL, body)
                .then(res => resolve(res))
                .catch(ex => reject(ex))
        })
    }


    // CHAT COMPLETIONS

    const roles = {
        system: 'system',
        user: 'user',
        assistant: 'assistant'
    };
    const defaultSystem = 'You are a helpful assistant.';
    const system = {
        role: roles.system,
        content: defaultSystem,
    };
    const chatModels = {
        GPT4: 'gpt-4',
        GPT35Turbo: 'gpt-3.5-turbo',
        GPT40314: 'gpt-4-0314',
        GPT432k: 'gpt-4-32k',
        GPT432k0314: 'gpt-4-32k-0314',
        GPT35Turbo0301: 'gpt-3.5-turbo-0301',
    }
    const [model, setModal] = useState(chatModels.GPT35Turbo);

    const [stream, setStream] = useState(true);

    const [messages, setMessages] = useState([system]);

    const getModels = () => Object.values(chatModels);

    const getSystemMessage = () => messages.find((e) => e.role === roles.system).content;


    const toggleStream = () => setStream(!stream)

    const setSystemMessage = (message) => {
        const newMessage = [...messages]
        newMessage.find((e) => e.role === roles.system).content = message;
        setMessages(newMessage);
    }

    const clearMessages = () => {
        const newMessage = [...messages]
        newMessage.length = 1;
        setMessages(newMessage);
    }

    const editMessage = (id, message) => {
        setMessages(prev => {
            const newMessage = [...prev]
            const index = newMessage.findIndex(e => e.id === id);
            if (index !== -1) {
                newMessage[index].content = message;
            }
            return [...newMessage];
        })
    }

    const addPropToMessage = (prop, element, id) => {
        setMessages(prev => {
            const newMessage = [...prev]
            newMessage.find(e => e.id === id)[prop] = element;
            return [...newMessage];
        })
    }

    const removeMessage = (id) => setMessages(prev => [...prev].filter(e => e.id !== id))

    const getLatestMessage = () => messages.at(-1);

    const buildMessage = message => {
        return {
            role: roles.user,
            content: message,
            id: generateId()
        }
    }

    const addMessage = (message) => {
        if (!message.id) {
            message.id = generateId();
        }
        setMessages((prevMessages) => [...prevMessages, message]);
    };

    const getMessages = () => messages
        .map((e) => ({
            role: e.role,
            content: e.content,
        }));


    const askChat = async (message) => {
        let msg = [...messages];

        if (message) {
            const newMsg = buildMessage(message)
            msg = [...messages, newMsg];
            addMessage(newMsg)
        }

        msg = msg.map((e) => ({
            role: e.role,
            content: e.content,
        }));

        return new Promise(async (resolve, reject) => {
            if (!API_KEY || !stream) {
                ask({ model: model, messages: msg, stream: false })
                    .then(res => {
                        const chatMessage = res.choices[0].message;
                        addMessage(chatMessage);
                        resolve(chatMessage);
                    })
                    .catch(ex => reject(ex))
            } else {
                try {
                    const response = await ask({ model: model, messages: msg, stream: stream });

                    if (!response.ok) {
                        throw new Error('Request failed with status: ' + response.status);
                    }

                    let res = { content: '', id: generateId() };
                    const readStream = async (reader) => {
                        const { done, value } = await reader.read();

                        if (done) {
                            resolve(res);
                            return;
                        }

                        new TextDecoder()
                            .decode(value)
                            .split('\n')
                            .map(e => e.replace(/^data: /, '').trim())
                            .filter((e) => e !== '' && e !== '[DONE]')
                            .map((e) => JSON.parse(e))
                            .forEach(e => {
                                if (!e.choices[0].finish_reason) {
                                    const chunk = e.choices[0].delta;
                                    if (chunk.role) {
                                        addMessage({ role: chunk.role, content: '', id: res.id });
                                    } else {
                                        res.content += chunk.content;
                                        editMessage(res.id, res.content);
                                    }
                                }
                            })
                        readStream(reader);
                    };
                    readStream(response.body.getReader());
                } catch (ex) {
                    reject(ex)
                }
            }
        })
    };

    // DALLE 

    const imageSizes = {
        small: '256x256',
        medium: '512x512',
        large: '1024x1024'
    }
    const [imageSize, setImageSizes] = useState(imageSizes.small);

    const getImageSizes = () => Object.entries(imageSizes).map(([key, value]) => value);

    const setImageSize = (size) => {
        console.log('Updating Image Size:', imageSizes[size])
        setImageSizes(size);
    }

    const generateImage = (prompt, number = 1, size = imageSize, format = 'b64_json') => {
        console.log('generating Image')
        return new Promise((resolve, reject) => {
            Kwest.post('https://api.openai.com/v1/images/generations', {
                prompt: prompt,
                n: number,
                size: size,
                response_format: format,
            }, {
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': 'Bearer ' + API_KEY,
                },
            })
                .then(res => {
                    const images = res.data.map(e => e[format]);
                    resolve(images.length > 1 ? images : images[0]);
                })
                .catch(ex => reject(ex))
        })
    }

    const downloadImageFromBase64 = (base64, fileName = 'Image') => {
        var link = document.createElement("a");
        link.href = "data:application/octet-stream;base64," + base64;
        link.download = `${fileName}.png`;
        link.click();
    }


    // JARVIS Model
    const [personalities, setPersonalities] = useState(null);
    const [personality, setPerson] = useState(null);
    const [plugins, setPlugins] = useState(null);
    const [actionEnums, setActionEnums] = useState(null);
    const [actionRequest, setActionRequest] = useState(null);
    const [spotify] = useState(new Spotify());


    const toggleExternal = (feature) => {
        if (typeof plugins[feature] !== 'boolean') return;
        console.log('Toggling:', feature);
        const newPlugs = structuredClone(plugins);
        newPlugs[feature] = !plugins[feature];
        setPlugins(newPlugs);
    }

    const getPersonalities = () => Object.values(personalities);

    const getPlugins = () => {
        return { ...plugins };
    }

    const setPersonality = (newPersonality = personality) => {
        if (newPersonality === personalities.JARVIS && Math.random() > 0.9) {
            console.log('Altering Personality:', newPersonality)
            newPersonality = personalities.JARVIS + '..'
        } else {
            console.log('Updating Personality:', newPersonality)
        }
        document.title = newPersonality;
        setPerson(newPersonality);
    }

    const buildAction = (action, id) => {
        const updateAction = {
            request: typeof action === 'string' ? JSON.parse(action) : action,
            messageId: id
        };
        setActionRequest(updateAction)
    }

    const getAction = () => {
        if (!actionRequest) return;

        const model = { ...actionRequest };
        setActionRequest(null);
        return model;
    }

    const askJARVIS = async (message) => {
        setInteraction(true);
        const lastMessage = await askChat(message);

        if (lastMessage.content.match('{')) {
            const content = lastMessage.content;
            const action = content.match(/\{.*?\}/)?.[0];
            const updateAction = {
                request: JSON.parse(action),
                messageId: lastMessage.id
            };
            addPropToMessage('action', action, lastMessage.id);
            setActionRequest(updateAction)
        } else {
            setInteraction(false);
        }
    }

    const [loaded, setLoaded] = useState(false);
    const [offline, setOffline] = useState(false);

    const initJARVIS = () => {
        const API_URL = process.env.REACT_APP_API_URL
        KwestGiver.get(API_URL + 'buildJARVIS')
            .then(res => {
                setPersonalities(res.personalities)
                setPerson(res.personalities.JARVIS)
                setPlugins(res.plugins)
                setActionEnums(res.actionEnums)

                setUrl(API_URL + 'askChat');
                setAPIKey(API_KEYS.GPT.KEY);
                spotify.setClients(API_KEYS.SPOTIFY.CLIENT_ID, API_KEYS.SPOTIFY.CLIENT_SECRET);
                spotify.setRefreshToken(API_KEYS.SPOTIFY.REFRESH_TOKEN);

                setLoaded(true);
            })
            .catch(ex => {
                console.warn(ex)
                setOffline(true)
            });
    }

    useLayoutEffect(() => {
        setInteraction(false);
        setError('');
        clearChat();
        initJARVIS();
    }, []);

    const askCHAT = async () => {
        try {
            setError(null);
            await askJARVIS(CHAT);
        } catch (ex) {
            console.log(ex)
            setError(ex?.error?.message || ex?.error?.code || 'Something went wrong...')
            toggleGPTEnabled(false)
            setInteraction(false);
        } finally {
            clearChat();
        }
    };

    useEffect(() => {
        setAPIKey(API_KEYS.GPT.KEY);
    }, [API_KEYS.GPT]);

    useEffect(() => {
        if (!CHAT || !CONFIG.GPT.ENABLED) return;
        askCHAT();
    }, [CHAT]);

    useEffect(() => {
        if (!actionRequest) return;
        performAction(actionRequest)
    }, [actionRequest]);

    const performAction = async (act) => {
        console.log(act)
        if (!act) {
            act = getAction()
            if (!act) {
                setInteraction(false)
                return;
            }
        }

        if (getPlugins().spotify && !spotify.getPlaybackDevice()) {
            const device = await spotify.requestAccess();
            if (!device) {
                setError('Playback not available or active.');
                setInteraction(false);
                return;
            }
        }

        if (!INTERACTING) setInteraction(true);

        const id = act.messageId;
        const request = act.request;
        const actions = actionEnums;

        console.log('Performing Action:', act);
        switch (request.action) {
            case actions.generate:
                requestAction(() =>
                    generateImage(request.prompt)
                        .then(res => {
                            addImage({
                                prompt: request.prompt,
                                image: res,
                                resolution: imageSize,
                            });
                            addPropToMessage('img', res, id);
                        })
                );
                break;
            case actions.playSong:
                requestAction(() => spotify.playAction(request.song, request.artist));
                break;
            case actions.playAlbum:
                requestAction(() => spotify.albumAction(request.album, request.artist));
                break;
            case actions.playArtist:
                requestAction(() => spotify.artistAction(request.artist));
                break;
            case actions.playLiked:
                requestAction(() => spotify.likedAction());
                break;
            case actions.skipSong:
                requestAction(() => spotify.skipTrack());
                break;
            case actions.restartSong:
                requestAction(() => spotify.replayTrack());
                break;
            case actions.openWebsite:
                requestAction(() => window.open(request.URL, '_blank'));
                break;
            case actions.openApplications:
                requestAction(() => window.open(request.application + ':', '_blank'));
                break;
            default:
                setInteraction(false);
                console.warn('Unknown Request', request);
        }
    }

    const requestAction = async (action) => {
        try {
            await action();
        } catch (ex) {
            console.log(ex)
            setError(ex?.error?.message || ex?.error?.code || 'Something went wrong...')
        } finally {
            setInteraction(false);
        }
    }

    const contextValue = {
        spotify,
        API_URL,
        API_KEY,
        model,
        roles,
        defaultSystem,
        messages,
        personalities,
        personality,
        plugins,
        actionEnums,
        actionRequest,
        stream,
        imageSize,
        toggleStream,
        askCHAT,
        performAction,
        clearMessages,
        editMessage,
        removeMessage,
        setModel,
        getModels,
        getPersonalities,
        setPersonality,
        toggleExternal,
        getSystemMessage,
        setSystemMessage,
        setImageSize,
        getImageSizes,
        downloadImageFromBase64,
        buildAction,
        initJARVIS,
        generateImage
    };

    return loaded
        ? (
            <JARVISContext.Provider value={contextValue}>
                {children}
            </JARVISContext.Provider>
        ) : (
            <Page>
                <Title icon={offline ? 'skull' : 'spinner'}>{offline ? 'Offline' : 'Initializing'}</Title>
            </Page>
        )
}

export default JARVIS;