import React, { useContext, useEffect, useState } from 'react';
import { useGlobalState } from '../stateProvider/StateProvider';
import Title from '../form/Title';
import Card from '../Card';
import { Idle } from '../JARVISicon';
import styled, { keyframes } from 'styled-components';
import Heading from '../form/Heading';
import { JARVISContext } from '../JARVIS';

const JARVISWrapper = styled.div`
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
`;

const glowing = keyframes`
    0% {
        box-shadow: 0 0 60px 60px #507c9b;
    }
    50% {
        box-shadow: 0 0 110px 110px #a6c2d5;
    }
    100% {
        box-shadow: 0 0 60px 60px #507c9b;
    }
`;
const InnerGlow = styled.div`
    border-radius: 50%;
    animation: ${glowing} ease-in-out 2s infinite;
    position: absolute;
`;
const CardCSS = styled(Card)`
    align-items: center;
`;

const Home = () => {
    const headlineText = `Hello and welcome to your AI digital assistant. Say hello!`
    const [headline, setHeadline] = useState('');

    const [icon] = useState(
        <JARVISWrapper>
            <InnerGlow />
            <Idle />
        </JARVISWrapper>
    );

    useEffect(() => {
        let index = 0;
        const intervalId = setInterval(() => {
            if (index < headlineText.length) {
                const newChar = headlineText.charAt(index);
                setHeadline((pre) => pre + newChar);
                index++;
            } else {
                clearInterval(intervalId);
            }
        }, 50);

        return () => clearInterval(intervalId);
    }, []);

    return (
        <CardCSS>
            <Title>{headline}</Title>
            {icon}
        </CardCSS>
    );
}

export default Home;