import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import styled, { keyframes } from 'styled-components';

const tickerAnimation = keyframes`
    0% {
        transform: translateX(${window.innerWidth < 800 ? window.innerWidth : window.innerWidth - 260}px);
    }
    100% {
        transform: translateX(-100%);
    }
`;

const Crawl = styled.div`
    animation: ${tickerAnimation} 10s linear infinite;
    white-space: nowrap;
    width: fit-content;
`;

const Crawler = ({ children, ...props }) => {

    const [width, setWidth] = useState(window.innerWidth)
    useEffect(() => {
        const handleResize = () => {
            setWidth(window.innerWidth);
        };
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);


    return (
        <Crawl width={width} {...props}>
            {children}
        </Crawl>
    );
};

export default Crawler;
