import React, { useContext, useEffect, useState } from 'react';
import styled from 'styled-components';
import Icon from './form/Icon';
import { JARVISContext } from './JARVIS';
import { BorderLessButton } from './form/Button';
import TextArea from './form/TextArea';
import Row from './layout/Row';

const Wrapper = styled.div`
    position: relative;
    display: flex;
    align-items: center;
    padding: 1em;
    border-radius: 0.2rem;

    width: -webkit-fill-available;

    max-height: ${({ deleting }) => deleting ? 0 : 1000}px;
    transition: all 0.2s;

    @media (max-width: 800px) {
        padding: 0;
        flex-direction: column;
        align-items: flex-start;
        margin-top: 0.5em;
    } 
    
    ${({ deleting }) => deleting ? `
        overflow: hidden;
        opacity: 0;
        ` : ''
    }
`

const Content = styled.div`
    margin: 0 1em;
    width: inherit;
    position: relative;
    display: flex;
    align-items: center;
    align-items: flex-start;
    flex-direction: column;
    border-radius: 0.2rem;
    background: ${({ theme }) => theme.lighter};

    ${({ action }) => action && `
    overflow: hidden;
    &:after {
        content: "";
        position: absolute;
        margin: -20px;
        width: 30px;
        height: 30px;
        transform: rotate(45deg);
        background-color: #a5ffac;
    }`}

    @media (max-width: 800px) {
        margin: unset;
    }
`

const ProfileWrapper = styled.div`
    display: flex;
    align-items: center;
    min-width: 2em;
    height: 2em;
    justify-content: center;

    @media (max-width: 800px) {
        left: -0.3em;
        min-width: 1em;
        height: 1em;
        margin: 0.2em;
    }
`
const Img = styled.img`
    width: 256px;
    height: 256px;
    margin: 1em;
    border-radius: 1rem;
`
const TextWrapper = styled(TextArea)`
    width: inherit;
`
const CloseIcon = styled(BorderLessButton)`
    position: absolute;
    right: -10px;
    @media (max-width: 800px) {
        position: unset;
        height: 1em;
    }
`
const RedoAction = styled(BorderLessButton)`
    position: absolute;
    right: 40px;
    z-index: 1;
    @media (max-width: 800px) {
        position: unset;
        height: 1em;
    }
`
const ActionRow = styled(Row)`
    @media (max-width: 800px) {
        width: 100%;
        justify-content: space-between;
    }
`

const Message = ({ role, content, profileImg, id, last, messageIndex, ...props }) => {
    const { buildAction, editMessage, removeMessage, roles } = useContext(JARVISContext);

    const [initial, setInitial] = useState(content.replace(/\{.*?\}|\{.+/g, '').trim());
    useEffect(() => {
        setInitial(content.replace(/\{.*?\}|\{.+/g, '').trim())
    }, [content])

    useEffect(() => {
        setValue(initial)
    }, [initial])

    useEffect(() => {
        setAction(props.action);
    }, [props.action])

    const [action, setAction] = useState(props.action);

    const [value, setValue] = useState(initial);
    const [focused, setFocused] = useState(false);
    const [deleting, setDeleting] = useState(false);

    const handleMessageUpdate = () => {
        editMessage(id, value);
    };

    const handleInput = (val) => {
        setValue(val)
    };

    const handleMouseOver = (bool) => {
        if (window.innerWidth > 800) setFocused(bool)
    }
    const handleFocus = (bool) => {
        if (window.innerWidth < 800) setFocused(bool)
    }
    const handleDeleting = () => {
        setDeleting(true);
        setTimeout(() => {
            removeMessage(id);
        }, 210)
    }

    return (
        <Wrapper
            onMouseEnter={() => handleMouseOver(true)}
            onMouseLeave={() => handleMouseOver(false)}

            title={action}
            tabIndex={messageIndex}

            deleting={deleting}
        >
            <ActionRow>
                <ProfileWrapper>
                    {role === roles.assistant ? profileImg : <Icon icon='user' />}
                </ProfileWrapper>
                <Row>
                    {(focused && action) && (
                        <RedoAction
                            icon='undo'
                            onClick={() => buildAction(action, id)}
                        />
                    )}
                    {focused && (
                        <CloseIcon
                            icon='cross'
                            onClick={handleDeleting}
                        />
                    )}
                </Row>
            </ActionRow>

            <Content
                action={action}
            >
                <TextWrapper
                    value={value}
                    onInput={(event) => handleInput(event.target.value)}

                    onFocus={() => handleFocus(true)}
                    onBlur={() => {
                        handleMessageUpdate();
                        setTimeout(() => handleFocus(false), 50)
                    }}
                />
                {props.img && <Img src={'data:image/png;base64, ' + props.img} />}
            </Content>
        </Wrapper>
    );
};

export default Message;