import styled from 'styled-components';
import Button from '../form/Button';
import { useEffect, useState } from 'react';
import Burger from '../Burger';
import { useGlobalState } from '../stateProvider/StateProvider';
import SpeechRecognition from '../api/SpeechRecognition';

const SideBarCSS = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100svh;
    min-width: 260px;
    max-width: 260px;
    background: ${({ theme }) => theme.darker};
    box-shadow: 0px 1px 20px 0px ${({ theme }) => theme.border};
    z-index: 10;
    transition: all 0.3s ease-out;

    @media (max-width: 800px) {
        position: absolute;
        left: 0;
        width: ${({ open }) => open ? 260 : 0}px;
        
        max-width: 260px;
        min-width: unset;
        box-shadow: unset;
        overflow: hidden;
    }
`
const Nav = styled.div`
    display: flex;
    flex-direction: column;

    margin: 0.5em;
    padding: 0.5em;
`
const SideFooter = styled(Nav)`
    padding-top: 0.5rem;
    border-top: 1px solid ${({ theme }) => theme.border};
`
const CloseArea = styled.div`
    position: absolute;
    background: red;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index:9;

    transition: all 0.2s;

    ${({ open }) => open ? `
    background: #00000060;
    ` : `
    background: #00000000;
    pointer-events: none;
    `}


`
const BurgerCSS = styled(Burger)`
    position: absolute;
    margin: 2.5em 2em;
    top: 0;
    right: 0;
    z-index: 100;
`

const SideBar = ({ height }) => {
    const {
        state: {
            LISTENING
        },
        addVoiceChat
    } = useGlobalState();

    const [open, setOpen] = useState(false);

    useEffect(() => {
        let posX1 = 0;
        let posX2 = 0;

        const touchDrag = (e) => posX2 = e.changedTouches[0].clientX;

        const touchEnd = () => {
            window.removeEventListener('touchend', touchEnd);
            window.removeEventListener('touchmove', touchDrag);
            if (!open && posX2 - posX1 > window.screen.width * 0.4) {
                setOpen(true);
            }
            posX1 = null;
            posX2 = null;
        };

        const touchStart = (e) => {
            posX1 = e.changedTouches[0].clientX;
            window.addEventListener('touchend', touchEnd);
            window.addEventListener('touchmove', touchDrag);
        };

        window.addEventListener('touchstart', touchStart);

        return () => {
            window.removeEventListener('touchstart', touchStart);
            window.removeEventListener('touchend', touchEnd);
            window.removeEventListener('touchmove', touchDrag);
        };
    }, [open, setOpen]);


    const NavButton = ({ children, ...props }) => {
        return (<Button
            onClick={() => setOpen(false)}
            {...props}
        >{children}</Button>)
    }

    return (
        <>
            {
                window.innerWidth < 800 &&
                <BurgerCSS
                    open={open}
                    onClick={() => setOpen(!open)}
                />
            }
            <CloseArea
                open={open}
                onClick={() => {
                    if (!open) return;
                    setOpen(false)
                }}
            />
            <SideBarCSS
                open={open}
                height={height}
            >
                <Nav>
                    <NavButton to='/' key='home'>Menu</NavButton>
                    <NavButton to='/JARVIS' key='JARVIS'>Assistant</NavButton>
                    <NavButton to='/Images' key='images'>Generated Images</NavButton>
                    <NavButton to='/Ants' key='ants'>Ants.js</NavButton>
                    <NavButton to='/Cars' key='cars'>Cars.js</NavButton>
                    <NavButton to='/ProKzy' key='prokzy'>ProKzy</NavButton>
                </Nav>
                <SideFooter>
                    {window.innerWidth > 800 &&
                        <SpeechRecognition
                            callback={addVoiceChat}
                            toggleSwitch={LISTENING}
                        />
                    }
                    <NavButton
                        to='/Settings'
                        icon='cog'
                    >Settings</NavButton>
                </SideFooter>
            </SideBarCSS>
        </>
    );
}

export default SideBar;