import React, { useState, useEffect } from 'react';
import Button from '../form/Button';
import styled from 'styled-components';
import { useGlobalState } from '../stateProvider/StateProvider';
import HotKeyListener from './HotKeyListener';

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
`

const SpeechRecognition = ({ callback, toggleSwitch, ...props }) => {
    const {
        state: {
            CONFIG
        },
        setInteraction
    } = useGlobalState();

    const [listening, setListening] = useState(false);
    const [recognition, setRecognition] = useState(null);

    const [finalTranscript, setFinalTranscript] = useState('');
    const [interimTranscript, setInterimTranscript] = useState('');

    const [outsideToggle, setOutsideToggle] = useState(toggleSwitch);

    useEffect(() => {
        if (!('webkitSpeechRecognition' in window)) {
            console.log('SpeechRecognition not available.');
            return;
        }
        const recognition = new window.webkitSpeechRecognition();
        recognition.continuous = true;
        recognition.interimResults = true;

        recognition.onstart = () => {
            setInteraction(true);
            setListening(true);
        }

        recognition.onend = () => {
            setListening(false);
            setInteraction(false);
            setFinalTranscript('');
            setInterimTranscript('');
        };

        recognition.onresult = (event) => {
            let interim = '';
            let final = '';
            for (let i = event.resultIndex; i < event.results.length; i++) {
                const transcript = event.results[i][0].transcript;

                if (event.results[i].isFinal) {
                    final += transcript;
                } else {
                    interim += transcript;
                }
            }
            setFinalTranscript((finalTranscript) => finalTranscript + ' ' + final);
            setInterimTranscript(interim);
        };
        setRecognition(recognition);
    }, []);

    useEffect(() => {
        let timeout;
        if (listening && finalTranscript.match(/\w/g)) {
            timeout = setTimeout(() => {
                callback(finalTranscript);
                stopRecognition();
            }, 1500);
        }
        return () => clearTimeout(timeout);
    }, [interimTranscript]);

    useEffect(() => {
        if (outsideToggle === toggleSwitch || !CONFIG.GPT.ENABLED) return;
        setOutsideToggle(toggleSwitch);
        toggleRecognition();
    }, [toggleSwitch]);

    const startRecognition = () => recognition.start();

    const stopRecognition = () => recognition.stop();

    const toggleRecognition = () => recognition ? listening ? stopRecognition() : startRecognition() : null;

    if (!CONFIG.GPT.ENABLED) return <p>GPT Disabled.</p>

    return (
        <Wrapper>
            <p>{listening && finalTranscript}</p>
            <p>{listening && interimTranscript}</p>
            {CONFIG.HOTKEY && <HotKeyListener/>}
            <Button
                onClick={toggleRecognition}
                icon={listening ? 'micSlash' : 'mic'}
            >
                {listening ? 'Cancel' : 'Click to Speak'}
            </Button>
        </Wrapper>
    );
}

export default SpeechRecognition;
