import React, { createContext, useReducer, useContext, useEffect, useState, } from 'react';
import { clearAllPersistedState, usePersistedState } from './UsePersistedState';
import { generateId } from '../utility/utility';

const initialState = {
    COLOURS: {
        main: '#343541',
        secondary: '#444654',
        lighter: '#40414f',
        darker: '#202123',
        text: '#d1d5d2',
        border: '#ffffff33',
        error: '#ff7979',
        success: '#79ff90',
    },
    CHAT: '',
    GENERATED_IMAGES: [],
    API_KEYS: {
        GPT: { KEY: process.env.REACT_APP_GPT_KEY, },
        SPOTIFY: {
            CLIENT_ID: process.env.REACT_APP_SPOTIFY_CLIENT_ID,
            CLIENT_SECRET: process.env.REACT_APP_SPOTIFY_CLIENT_SECRET,
            REFRESH_TOKEN: null,
        }
    },
    TOKENS: {
        SPOTIFY: null
    },
    CONFIG: {
        GPT: { ENABLED: true, },
        HOTKEY: false,
    },
    LISTENING: false,
    INTERACTING: false,
    ERROR: '',
    VERSION: '0.0.1'
}

export const GlobalContext = createContext(initialState);

const StateProvider = ({ children, initialValues = initialState }) => {
    // clearAllPersistedState();

    const [persistedState, setPersistedState] = usePersistedState('global', initialValues);
    const [state, dispatch] = useReducer(Reducer, persistedState);

    useEffect(() => {
        setPersistedState(state);
    }, [setPersistedState, state]);

    return (
        <GlobalContext.Provider value={{ state, dispatch }}>
            {children}
        </GlobalContext.Provider>
    );
}

const ACTION_TYPES = {
    SET_GPT_KEY: 'SET_GPT_KEY',
    SET_SPOTIFY_CLIENT_ID: 'SET_SPOTIFY_CLIENT_ID',
    SET_SPOTIFY_CLIENT_SECRET: 'SET_SPOTIFY_CLIENT_SECRET',
    SET_SPOTIFY_REFRESH_TOKEN: 'SET_SPOTIFY_REFRESH_TOKEN',
    ADD_CHAT: 'ADD_CHAT',
    CLEAR_CHAT: 'CLEAR_CHAT',
    ADD_IMAGE: 'ADD_IMAGE',
    REMOVE_IMAGE: 'REMOVE_IMAGE',
    SET_LISTENING: 'SET_LISTENING',
    SET_INTERACTION: 'SET_INTERACTION',
    SET_ERROR: 'SET_ERROR',
    TOGGLE_GPT: 'TOGGLE_GPT',
    TOGGLE_HOTKEY: 'TOGGLE_HOTKEY',
};

export function useGlobalState() {
    const { state, dispatch } = useContext(GlobalContext);

    const [functions] = useState({
        setGPTKey: (key) => {
            dispatch({ type: ACTION_TYPES.SET_GPT_KEY, payload: key });
        },

        setSpotifyClientId: (key) => {
            dispatch({ type: ACTION_TYPES.SET_SPOTIFY_CLIENT_ID, payload: key });
        },
        setSpotifyClientSecret: (key) => {
            dispatch({ type: ACTION_TYPES.SET_SPOTIFY_CLIENT_SECRET, payload: key });
        },
        setSpotifyRefreshToken: (key) => {
            dispatch({ type: ACTION_TYPES.SET_SPOTIFY_REFRESH_TOKEN, payload: key });
        },

        addImage: (img) => {
            img.id = generateId();
            dispatch({ type: ACTION_TYPES.ADD_IMAGE, payload: img });
        },
        removeImage: (id) => {
            dispatch({ type: ACTION_TYPES.REMOVE_IMAGE, payload: id });
        },

        addVoiceChat: (chat) => {
            chat = chat.trim().replace(/ +/g, ' ');
            dispatch({ type: ACTION_TYPES.ADD_CHAT, payload: chat });
        },
        clearChat: () => {
            dispatch({ type: ACTION_TYPES.CLEAR_CHAT, });
        },

        setInteraction: (interaction) => {
            dispatch({ type: ACTION_TYPES.SET_INTERACTION, payload: interaction });
        },
        setError: (error) => {
            dispatch({ type: ACTION_TYPES.SET_ERROR, payload: error });
        },

        toggleListening: () => {
            dispatch({ type: ACTION_TYPES.SET_LISTENING, });
        },
        toggleGPTEnabled: (bool) => {
            dispatch({ type: ACTION_TYPES.TOGGLE_GPT, payload: bool });
        },
        toggleHotkey: () => {
            dispatch({ type: ACTION_TYPES.TOGGLE_HOTKEY });
        },
    });

    return {
        ...functions,
        state,
    };
}

export default StateProvider;

const Reducer = (state = initialState, action) => {
    const newState = structuredClone(state);
    switch (action.type) {
        case ACTION_TYPES.SET_GPT_KEY:
            newState.API_KEYS.GPT.KEY = action.payload;
            break;

        case ACTION_TYPES.SET_SPOTIFY_CLIENT_ID:
            newState.API_KEYS.SPOTIFY.CLIENT_ID = action.payload;
            break;

        case ACTION_TYPES.SET_SPOTIFY_CLIENT_SECRET:
            newState.API_KEYS.SPOTIFY.CLIENT_SECRET = action.payload;
            break;

        case ACTION_TYPES.SET_SPOTIFY_REFRESH_TOKEN:
            newState.API_KEYS.SPOTIFY.REFRESH_TOKEN = action.payload;
            break;

        case ACTION_TYPES.ADD_CHAT:
            newState.CHAT = action.payload;
            break;

        case ACTION_TYPES.CLEAR_CHAT:
            newState.CHAT = '';
            break;

        case ACTION_TYPES.ADD_IMAGE:
            newState.GENERATED_IMAGES.unshift(action.payload);
            newState.GENERATED_IMAGES.length = 10;
            break;

        case ACTION_TYPES.REMOVE_IMAGE:
            newState.GENERATED_IMAGES = newState.GENERATED_IMAGES.filter(e => e.id !== action.payload);
            break;

        case ACTION_TYPES.SET_LISTENING:
            newState.LISTENING = !newState.LISTENING;
            break;

        case ACTION_TYPES.SET_INTERACTION:
            newState.INTERACTING = action.payload;
            break;

        case ACTION_TYPES.SET_ERROR:
            newState.ERROR = action.payload;
            break;

        case ACTION_TYPES.TOGGLE_GPT:
            newState.CONFIG.GPT.ENABLED = typeof action.payload === 'boolean' ? action.payload : !state.CONFIG.GPT.ENABLED;
            break;

        case ACTION_TYPES.TOGGLE_HOTKEY:
            newState.CONFIG.HOTKEY = !newState.CONFIG.HOTKEY;
            break;

        default:
            console.error(`Unknown action type: ${action.type}`);
            throw new Error();
    }
    return newState;
};
