import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import Button, { BorderLessButton } from '../form/Button';

const Frame = styled.iframe`
    height: 100vh;
    width: 100%;
    border: unset;
`

const CloseFrame = styled(BorderLessButton)`
    position: absolute;
    top: 0;
    right: 0;
    margin: 1em;
    z-index: 9999;
`

const P5Frame = ({ ...props }) => {
    const [fullscreen, setFullscreen] = useState(false);
    const frameRef = useRef(null);

    useEffect(() => {
        const handleFullscreenChange = async () => {
            setFullscreen(!fullscreen);
            await delay(200);
            let keys = [props.reDraw || 'C']
            if (props.additionalKeys) {
                keys = [...keys, ...props.additionalKeys]
            }
            keys.forEach((e) => {
                frameRef.current.contentWindow.keyPressed(e);
            });
        };

        document.addEventListener('fullscreenchange', handleFullscreenChange);
        return () => {
            document.removeEventListener('fullscreenchange', handleFullscreenChange);
        };
    }, []);

    const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

    const toggleFullscreen = async () => {
        const frameElement = frameRef.current;

        if (!document.fullscreenElement) {
            if (frameElement.requestFullscreen) {
                frameElement.requestFullscreen();
            } else if (frameElement.mozRequestFullScreen) {
                frameElement.mozRequestFullScreen();
            } else if (frameElement.webkitRequestFullscreen) {
                frameElement.webkitRequestFullscreen();
            } else if (frameElement.msRequestFullscreen) {
                frameElement.msRequestFullscreen();
            }
        }
    };

    return (
        <>
            {/* {fullscreen && <CloseFrame icon='cross' onclick={() => document.exitFullscreen()} />} */}
            <Button onClick={toggleFullscreen}>Fullscreen</Button>
            <Frame
                title={props.title}
                src={props.src}
                ref={frameRef}
                allowFullScreen
            />
        </>
    );
};

export default P5Frame;
