import styled from 'styled-components';
import { InputCss } from './form/Input';
import { useEffect, useRef, useState } from 'react';
import Button, { BorderLessButton } from './form/Button';

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0.5em 0;
`

const ICSS = styled(InputCss)`
    width: 20vw;
    margin-right: 2em;
    cursor: pointer;
    @media (max-width: 800px) {
        width: 100%;
        margin-right: unset;
    }
`
const Row = styled.div`
    display: flex;
    align-items: center;

`
const InputRow = styled(Row)`
    height: 3em;
    @media (max-width: 800px) {
        margin: 0.2em 0;
        flex-wrap: wrap;
        height: auto;
    }
`
const ButtonRow = styled.div`
    display: flex;
    @media (max-width: 800px) {
        width: 100%;
        justify-content: space-between;
    }
`
const InputWrapper = styled.div`
    @media (max-width: 800px) {
        width: 100%;
        display: flex;
    }
`
const ButtonCSS = styled(BorderLessButton)`
    margin: 0em 0.5em;
`

export const SpanLabel = styled.span`
    margin-right: 1em;
    width: 100px;
`;

const APIKeyInput = ({ label, update, value, ...props }) => {
    const [editing, setEditing] = useState(false)
    const [val, setVal] = useState('');
    const inputRef = useRef(null)

    useEffect(() => {
        if (editing && inputRef.current) {
            inputRef.current.focus();
        }
    }, [editing])

    const handleInput = (e) => setVal(e.target.value);

    const handleSubmit = () => {
        setEditing(false);
        update(val);
        setVal('');
    }

    const handlePaste = async () => {
        const text = await navigator.clipboard.readText();
        update(text.trim());
        setEditing(false);
        setVal('');
    }

    const maskInput = (value) => value ? value.length > 7 ? `${value.substring(0, 3)}...${value.slice(-4)}` : value : '';

    return (
        <Wrapper
            {...props}
        >
            <form
                style={{ width: '100%' }}
                onSubmit={e => {
                    e.preventDefault()
                    handleSubmit()
                }}>
                <InputRow>
                    <SpanLabel>{label}</SpanLabel>
                    <InputWrapper
                        onClick={() => setEditing(true)}
                    >
                        <ICSS
                            value={editing ? val : maskInput(value)}
                            onChange={handleInput}
                            readOnly={!editing}
                            ref={inputRef}
                            placeholder={props.placeholder}
                        />
                    </InputWrapper>

                    <ButtonRow>
                        {
                            editing &&
                            <>
                                <div>
                                    <ButtonCSS
                                        title='Cancel'
                                        onClick={() => setEditing(false)}
                                        icon='cross'
                                    />
                                </div>
                                <Row>
                                    <ButtonCSS
                                        title='Paste from Clipboard'
                                        onClick={() => handlePaste()}
                                        icon='paste'
                                    />
                                    <ButtonCSS
                                        title='Save'
                                        onClick={() => handleSubmit()}
                                        icon='save'
                                    />
                                </Row>
                            </>
                        }
                    </ButtonRow>
                </InputRow>
            </form>

        </Wrapper>
    );
}

export default APIKeyInput;
