import { useEffect, useRef } from 'react';
import styled, { keyframes } from 'styled-components';

const slideIn = () => keyframes`
    from {
        opacity: 0;
        transform: translateX(101%);
    }
    to {
        opacity: 1;
        transform: translateX(0%);
    }
`

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 2em;
    border-radius: 1rem;
    background: ${({ theme }) => theme.secondary};
    transition: all 0.2s;
    overscroll-behavior: contain;
    overflow: overlay;
    max-height: calc(100% - 4em);
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.40);

    animation: ${slideIn} 0.3s;

    @media (max-width: 800px) {
        // padding: 1em 0;
        border-radius: 0.3rem;
        align-items: stretch;
    }
`

const Card = ({ children, ...props }) => {
    const divRef = useRef(null);
    useEffect(() => {
        // if (props.scrollBottom && Math.abs(divRef.current.scrollHeight - divRef.current.scrollTop - divRef.current.clientHeight) < 50) {
        if (props.scrollBottom) {
            divRef.current.scrollTop = divRef.current.scrollHeight;
        }
    }, [children]);

    return (
        <Wrapper ref={divRef} {...props}>
            {children}
        </Wrapper>
    );
}

export default Card;
