import React from 'react';
import styled from 'styled-components';
import { pascalCase } from '../utility/utility';
import Icon from './Icon';
import { Link, useLocation } from 'react-router-dom';

const Wrapper = styled.button`
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    display: flex;
    height: 3rem;
    align-items: center;
    padding: 0.3rem 1rem;
    border-radius: 0.2em;
    border: 1px solid ${({ theme }) => theme.border};
    transition: all 0.2s;
    margin: 0.2rem 0.2rem;;
    overflow: hidden;
    white-space: nowrap;

    background: ${({ theme, match }) => match ? theme.lighter : ''};

    &:hover {
        background: ${({ theme, match }) => !match ? theme.lighter + '70' : ''};
    }
    &:active {
        background: ${({ theme }) =>  theme.lighter};
    }
`;
const I = styled(Icon)`
`;
const IWrapper = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    
    min-width: 1rem;
    margin: 0 0.5rem;
`;

const L = styled(Link)`
    all: unset;
    display: flex;
    flex-direction: column;
`

const LinkTo = ({ ...props }) => {
    const location = useLocation();
    const to = props.to;
    delete props.to;
    return (
        <L to={to}>
            <Button {...props} currentLoc={location.pathname === to} />
        </L>
    )
}

const Button = ({ ...props }) => {
    return props.to
        ? <LinkTo {...props} />
        : (
            <Wrapper
                type='button'
                match={props.currentLoc}
                {...props}
            >
                {props.icon ?
                    <IWrapper>
                        <I icon={props.icon} />
                    </IWrapper> : null}
                {props.children ? pascalCase(props.children) : null}
            </Wrapper>
        )
};

export default Button;

export const BorderLessButton = styled(Button)`
    border: unset;
    padding: 0;
    margin: 0;
    height: 30px;
    opacity: 0.5;
    transition: all 0.2s;
    border-radius: 0.5rem;
    &:hover {
        opacity: 1;
    }
`