import React from 'react';
import styled from 'styled-components';
import { pascalCase } from '../utility/utility';
import Icon from './Icon';

const Wrapper = styled.div`
    font-size: 1.2rem;
    font-weight: 600;
    padding: 0.2em 0;
`;
const I = styled(Icon)`
    margin-right: 0.3em;
`;

const Heading = ({ children, ...props }) => (
    <Wrapper {...props}>
        {props.icon ? <I icon={props.icon} /> : null}
        {typeof children === 'string' ? pascalCase(children) : children}
    </Wrapper>
);


export default Heading;