import React from 'react';
import styled from 'styled-components';
import Row from '../layout/Row';
import { SpanLabel } from '../APIKeyInput';

const SelectCss = styled.select`
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    outline: inherit;
    display: flex;
    height: 2rem;
    align-items: center;
    padding: 0.3rem 1rem;
    border-radius: 0.2em;
    transition: all 0.2s;
    margin: 0.2rem 0;
    overflow: hidden;
    white-space: nowrap;

    background: ${({ theme }) => theme.lighter};
    &:hover {
        cursor: pointer;
    }

`;

const Wrapper = styled(Row)`
`;


const Select = ({ ...props }) => {
    return (
        <Wrapper>
            <SpanLabel>{props.label}</SpanLabel>
            <SelectCss {...props} />
        </Wrapper>
    )
};

export default Select;